<script setup>
import { computed, ref, defineExpose } from "vue";
// 状态管理
import { useGlobalStore } from "@/store/global";
let store = useGlobalStore();

// 时差偏移角度
let parallaxCheapXD = ref(0);
let parallaxCheapYD = ref(0);
// 时差xy偏移
let parallaxCheapX = ref(0);
let parallaxCheapY = ref(0);

// 屏幕可视区域高度
let clientHeight = computed(() => document.documentElement.clientHeight);
// 屏幕可视区域宽度
let clientWidth = computed(() => document.documentElement.clientWidth);

// 鼠标移动
let homeMouseMove = (e) => {
    let { screenX, screenY } = e;

    // 角度比例
    let YdegProportion = 0.0015;
    let XdegProportion = 0.0001;
    // 偏移比例
    let xyProportion = 0.008;

    // 鼠标在左边
    if (screenX < clientWidth.value / 2) {
        parallaxCheapYD.value = -(screenX * YdegProportion);
        parallaxCheapX.value = -(screenX * xyProportion);
    } else {
        // 鼠标在右边
        parallaxCheapYD.value = screenX * YdegProportion;
        parallaxCheapX.value = screenX * xyProportion;
    }

    // 鼠标在上边
    if (screenY < clientHeight.value / 2) {
        parallaxCheapXD.value = -(screenY * XdegProportion);
        parallaxCheapY.value = screenY * xyProportion;
    } else {
        // 鼠标在下边
        parallaxCheapXD.value = screenY * XdegProportion;
        parallaxCheapY.value = -(screenY * xyProportion);
    }
};

defineExpose({
    homeMouseMove,
});
</script>

<style scoped lang="scss">
