    .screen-svg {
        --screen-c-0: #e5e9ff;
        --screen-c-1: #d5dcff;
        --screen-c-2: white;
        --screen-c-3: #f1f4ff;
        --screen-c-4: var(--screen-c-3);
        --screen-c-5: #bdd8ff;
        --screen-c-6: #eaefff;
    }

    .people-svg {
        --people-c-0: #ece6e6;
