        --people-c-3: #190e02;
        --people-c-4: #819af4;
        --people-c-5: #3f3c7e;
        --people-c-6: #504d84;
        --people-c-7: #504d84;
        --people-c-8: #819af4;
        --people-c-9: #f1f7ff;
    }

    .left-layer-svg {
        --ll-c-0: #5c97ee;
